* {
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

/*Home.jsx CSS*/
.home-background {
  background-image: url("./imgs/brick-v3.jpg");
  background-size: cover;

  background-position: center;
  background-repeat: no-repeat;
  /* height: calc(100vh - 78px); */
  height: 100%;
}
.neonTextWelcome {
  color: #fff;
  text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #f40,
    0 0 82px #f40, 0 0 92px #f40, 0 0 102px #f40, 0 0 151px #f40;
  font-family: "Exo 2", sans-serif;
}
.neonWelcomeBorder {
  border: 0.2rem solid #fff;
  box-shadow: 0 0 0.2rem #fff, inset 0 0 0.2rem #fff, 0 0 1.7rem #08f,
    inset 0 0 1rem #08f, 0 0 1.7rem #08f, inset 0 0 1.7rem #08f;
}
.neonText {
  color: #fff;
  text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #08f,
    0 0 82px #08f, 0 0 92px #08f, 0 0 102px #08f, 0 0 151px #08f;
  animation: flicker 1.5s infinite alternate;
}
@keyframes flicker {
  0%,
  18%,
  22%,
  25%,
  53%,
  57%,
  100% {
    text-shadow: 0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 40px #08f,
      0 0 80px #08f, 0 0 90px #08f, 0 0 100px #08f, 0 0 150px #08f;
  }

  20%,
  24%,
  55% {
    text-shadow: none;
  }
}

/*NavBar.jsx CSS*/
.nav-slideIn {
  animation: slideIn 500ms;
  color: white;
}
.nav-slideOut {
  animation: slideOut 500ms;
}

@keyframes slideIn {
  from {
    top: -50vh;
  }
  to {
    top: 78px;
  }
}
@keyframes slideOut {
  from {
    top: 78px;
  }
  to {
    top: -50vh;
  }
}

/*Shop.jsx CSS*/
.shop-background {
  background-image: url("./imgs/brick-v3.jpg");
  background-size: cover;

  background-position: center;
  background-repeat: no-repeat;
  /* height: calc(100vh - 78px); */
}
.gameShopBorder {
  border: 0.2rem solid #fff;
  box-shadow: 0 0 0.2rem #fff, inset 0 0 0.2rem #fff, 0 0 1.3rem #d402d6,
    inset 0 0 1rem #d402d6, 0 0 1.3rem #d402d6, inset 0 0 1.7rem #d402d6;
  animation: pulsateGameBorder 0.11s ease-in-out infinite alternate;
}

@keyframes pulsateGameBorder {
  100% {
    box-shadow: 0 0 0.2rem #fff, inset 0 0 0.2rem #fff, 0 0 1.2rem #d402d6,
      inset 0 0 1rem #d402d6, 0 0 1.2rem #d402d6, inset 0 0 1.6rem #d402d6;
  }

  0% {
    box-shadow: 0 0 0.1rem #fff, inset 0 0 0.1rem #fff, 0 0 1.2rem #d402d6,
      inset 0 0 1rem #d402d6, 0 0 1.2rem #d402d6, inset 0 0 1.5rem #d402d6;
  }
}

.neonGame {
  color: #fff;
  text-shadow: 0 0 7px #fff, 0 0 8px #fff, 0 0 10px #d7035a, 0 0 15px #d7035a,
    0 0 21px #d7035a, 0 0 35px #d7035a, 0 0 40px #d7035a, 0 0 50px #d7035a;
  font-family: "Tilt Neon", cursive;
  animation: pulsateGame 0.11s ease-in-out infinite alternate;
}
.neonShop {
  color: #fff;
  text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 15px #0105df, 0 0 21px #0105df,
    0 0 35px #0105df, 0 0 40px #0105df, 0 0 50px #0105df;
  font-family: "Tilt Neon", cursive;
  animation: pulsateShop 0.11s ease-in-out infinite alternate;
}

@keyframes pulsateShop {
  100% {
    /* Larger blur radius */
    text-shadow: 0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 30px #0105df,
      0 0 40px #0105df, 0 0 50px #0105df, 0 0 60px #0105df;
  }
  0% {
    /* A slightly smaller blur radius */
    text-shadow: 0 0 4px #fff, 0 0 10px #fff, 0 0 18px #fff, 0 0 24px #0105df,
      0 0 30px #0105df, 0 0 40px #0105df, 0 0 50px #0105df;
  }
}
@keyframes pulsateGame {
  100% {
    /* Larger blur radius */
    text-shadow: 0 0 4px #fff, 0 0 11px #fff, 0 0 19px #fff, 0 0 30px #d7035a,
      0 0 40px #d7035a, 0 0 50px #d7035a, 0 0 60px #d7035a;
  }
  0% {
    /* A slightly smaller blur radius */
    text-shadow: 0 0 4px #fff, 0 0 10px #fff, 0 0 18px #fff, 0 0 24px #d7035a,
      0 0 30px #d7035a, 0 0 40px #d7035a, 0 0 50px #d7035a;
  }
}

/*GuestBook.jsx CSS*/
.bubble {
  position: relative;
  width: 300px;
  height: 30%;
  text-align: center;
  line-height: 1.4em;
  margin: 40px auto;
  background-color: #e0e0e0;
  border: 8px solid #e0e0e0;
  border-radius: 30px;
  font-family: sans-serif;
  padding: 20px;
  font-size: large;
}

.bubble:before,
.bubble:after {
  content: " ";
  position: absolute;
  width: 0;

  height: 0;
}

.speech:before {
  left: 30px;
  bottom: -50px;
  border: 25px solid;

  border-color: #e0e0e0 transparent transparent #e0e0e0;
}

.speech:after {
  left: 38px;
  bottom: -30px;
  border: 15px solid;

  border-color: #e0e0e0 transparent transparent #e0e0e0;
}

.dots-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
}

.dot {
  height: 20px;
  width: 20px;
  margin-right: 10px;
  border-radius: 10px;
  background-color: #b1b1b1;
  animation: pulse 1.5s infinite ease-in-out;
}

.dot:last-child {
  margin-right: 0;
}

.dot:nth-child(1) {
  animation-delay: -0.3s;
}

.dot:nth-child(2) {
  animation-delay: -0.1s;
}

.dot:nth-child(3) {
  animation-delay: 0.1s;
}

@keyframes pulse {
  0% {
    transform: scale(0.8);
    background-color: #b1b1b1;
    box-shadow: 0 0 0 0 rgba(178, 212, 252, 0.7);
  }

  50% {
    transform: scale(1.2);
    background-color: #3b3b3b;
    box-shadow: 0 0 0 10px rgba(178, 212, 252, 0);
  }

  100% {
    transform: scale(0.8);
    background-color: #b1b1b1;
    box-shadow: 0 0 0 0 rgba(178, 212, 252, 0.7);
  }
}

/*SignIn/SignUp css*/
.invalidForm {
  animation: shake 0.2s ease-in-out 0s 2;
}

@keyframes shake {
  0% {
    margin-left: 0rem;
  }
  25% {
    margin-left: 0.35rem;
  }
  75% {
    margin-left: -0.35rem;
  }
  100% {
    margin-left: 0rem;
  }
}

