/*Game.jsx CSS*/

.game-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  column-gap: 4.5rem;
  row-gap: 1rem;
}

.chessOnlineImg {
  transition: all ease-in-out 200ms;
  /* background-position: 100% 0; */
  /* background-repeat: no-repeat; */
  background-image: url("./imgs/rookBlack.png");
  object-fit: fill;
  background-position: -10% 100%;
  background-size: contain;
  /* background-size: 45% 50%; */
  background-color: #ebecf0;
  background-repeat: no-repeat;
}
.chessOnlineImg:hover {
  background-image: url("./imgs/rookWhite.png");
  background-position: 110% 0;
  transform: scale(1.05);
}

.chessLocalImg {
  transition: all ease-in-out 200ms;

  background-image: url("./imgs/kingBlack.png"), url("./imgs/kingWhite.png");
  object-fit: fill;
  background-position: -10% 100%, 110% 100%;
  background-size: contain, contain;

  background-color: #ebecf0;
  background-repeat: no-repeat;
}
.chessLocalImg:hover {
  background-image: url("./imgs/kingWhite.png"), url("./imgs/kingBlack.png");
  transform: scale(1.05);
}

.comingSoonImg {
  transition: all ease-in-out 200ms;
  background-color: #ebecf0;
}
.comingSoonImg:hover {
  transform: scale(1.05);
}

/*ChessGameServer.jsx CSS*/
.chessOnline-grid {
  display: flex;
  flex-wrap: wrap-reverse;
}
.chessboard {
  flex-grow: 2;
}
.chessboard-side {
  min-width: 300px;
  flex-grow: 1;
}

.three-body {
  --uib-size: 35px;
  --uib-speed: 0.8s;
  --uib-color: #5d3fd3;
  position: relative;
  display: inline-block;
  height: var(--uib-size);
  width: var(--uib-size);
  animation: spin78236 calc(var(--uib-speed) * 2.5) infinite linear;
}

.three-body__dot {
  position: absolute;
  height: 100%;
  width: 30%;
}

.three-body__dot:after {
  content: "";
  position: absolute;
  height: 0%;
  width: 100%;
  padding-bottom: 100%;
  background-color: var(--uib-color);
  border-radius: 50%;
}

.three-body__dot:nth-child(1) {
  bottom: 5%;
  left: 0;
  transform: rotate(60deg);
  transform-origin: 50% 85%;
}

.three-body__dot:nth-child(1)::after {
  bottom: 0;
  left: 0;
  animation: wobble1 var(--uib-speed) infinite ease-in-out;
  animation-delay: calc(var(--uib-speed) * -0.3);
}

.three-body__dot:nth-child(2) {
  bottom: 5%;
  right: 0;
  transform: rotate(-60deg);
  transform-origin: 50% 85%;
}

.three-body__dot:nth-child(2)::after {
  bottom: 0;
  left: 0;
  animation: wobble1 var(--uib-speed) infinite calc(var(--uib-speed) * -0.15)
    ease-in-out;
}

.three-body__dot:nth-child(3) {
  bottom: -5%;
  left: 0;
  transform: translateX(116.666%);
}

.three-body__dot:nth-child(3)::after {
  top: 0;
  left: 0;
  animation: wobble2 var(--uib-speed) infinite ease-in-out;
}

@keyframes spin78236 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes wobble1 {
  0%,
  100% {
    transform: translateY(0%) scale(1);
    opacity: 1;
  }

  50% {
    transform: translateY(-66%) scale(0.65);
    opacity: 0.8;
  }
}

@keyframes wobble2 {
  0%,
  100% {
    transform: translateY(0%) scale(1);
    opacity: 1;
  }

  50% {
    transform: translateY(66%) scale(0.65);
    opacity: 0.8;
  }
}

.opponentMoveBtn {
  position: relative;
}
.opponentMoveBtn::before {
  position: absolute;
  content: "";
  bottom: -5px;
  left: -5px;
  height: 20px;
  width: 20px;
  border-radius: 2rem;
  background-color: #aaaaaa;
}
.loader {
  display: flex;
  justify-content: space-evenly;
}

.ball {
  list-style: none;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #616161;
}

.ball:nth-child(1) {
  animation: bounce-1 1.5s ease-in-out infinite;
}

@keyframes bounce-1 {
  50% {
    transform: translateY(-7px);
  }
}

.ball:nth-child(2) {
  animation: bounce-3 1.5s ease-in-out 0.3s infinite;
}

@keyframes bounce-2 {
  50% {
    transform: translateY(-7px);
  }
}

.ball:nth-child(3) {
  animation: bounce-3 1.5s ease-in-out 0.6s infinite;
}

@keyframes bounce-3 {
  50% {
    transform: translateY(-7px);
  }
}
